.ms-info {

    display: flex;
    justify-content: space-between;

    @media (max-width: 767px) {
        flex-direction: column;
        margin-top: 30px;
    }

    .ms-info__logo {
        @media (max-width: 991px) {
            img {
                max-height: 100px;
            }
        }

        @media (max-width: 767px) {
            margin-top: 30px;

            img {
                max-width: 120px;
            }
        }
    }

    h1 {
        font-family: 'AvantGarde Normal', serif;
        text-transform: uppercase;
        font-size: 48px;
        color: #909193;
        margin-bottom: 0;
        line-height: 1;

        @media (max-width: 991px) {
            font-size: 38px;
        }
    }

