            display: block;

            + .star-rating__star {
                margin-left: 5px;
            }
        }
    }

    .location {
        color: rgba(#49484D, .53);
        display: flex;
        background-image: url('../../images/location-pin.svg');
        background-repeat: no-repeat;
        background-size: 18px 24px;
        margin-top: 1.7rem;
        height: 24px;
        align-items: center;
        padding-left: 30px;
        font-size: 16px;
        background-position: left center;
    }
}